// 
export const generateStyle = (obj) => {
    let newObj = { ...obj }
    if (newObj.FontBold) {
        newObj.fontWeight = 'bold'
    } else {
        newObj.fontWeight = 'normal'
    }
    if (newObj.FontItalic) {
        newObj.fontStyle = 'italic'
    } else {
        newObj.fontStyle = 'normal'
    }
    delete newObj.FontBold
    delete newObj.FontItalic
    return newObj
}

// 
export const debounce = (fn, delay, scope) => {
    //  delay  fn 
    return function () {
        //  fn
        var context = this
        var args = arguments
        //  fn
        clearTimeout(scope.timer)
        // 
        //  delay  fn
        scope.timer = setTimeout(function () {
            fn.apply(context, args)
        }, delay)
    }
}


/**
|--------------------------------------------------
| urlsearch
|--------------------------------------------------
*/
export const parseUrlSearch=()=>{
    let href = window.location.href;
    let paramsIndex = '';
    let searchObj = {};
    if (href.indexOf('?') > -1) {            
        paramsIndex = href.indexOf("?") + 1;
        let params = href.substr(paramsIndex).split('&');
        if (params && params.length > 0) {
            params.forEach(
                (item) => {
                    let arr = item.split('=');
                    searchObj[arr[0]] = arr[1]
                }
            )    
        }
    }
    return searchObj
}